/* Drag and drop placeholder */
.ld__builder--placeholder {
	align-items: center;
	background: $porcelain;
	border: 2px dashed $iron;
	box-sizing: border-box;
	color: #666;
	display: flex;
	flex-direction: column;
	min-height: 40px;
	justify-content: center;
	margin: 20px 10px;
	position: relative;
	width: calc(100% - 20px);

	li {
		width: 100%;
	}

	&:not( .-is-empty ) {
		&:after {
			content: attr( data-label );
			display: block;
			position: absolute;
			text-align: center;
		}
	}

	&.-lesson {
		margin: 10px 0;
		width: 100%;
	}
}

.ld__builder--grandchild-item + .ld__builder--placeholder {
	margin-top: 10px;
}

.ld__builder--final-quizzes > h3 {
	text-transform: uppercase;
}

.ld__builder--final-quizzes .ld__builder--placeholder {
	margin: 10px 0;
	width: 100%;
}

.-is-dragging-over > .ld__builder--placeholder {
	background: #f1f1f1;
	border-color: $verdun_green;
}

.-is-dragging {
	border: 1px dashed #b4b9be;
	display: block;
}

.-is-dragging input,
.-is-dragging button {
	display: none;
}

.-disable-sort {
	transform: none !important;
}

/* Row type */
.ld__builder-row-type {
	align-items: center;
	border-radius: 100%;
	color: white;
	display: inline-flex;
	font-weight: bold;
	height: 25px;
	justify-content: center;
	margin-right: 10px;
	min-width: 25px;
	text-transform: uppercase;
	width: 25px;

	&.-parent {
		background: $code-parent;
		&.sfwd-question {
			background: $code-question;
		}
	}

	&.-child {
		background: $code-child;
		font-size: 10px;
		height: 18px;
		min-width: 18px;
		width: 18px;
		&.sfwd-answer {
			background: $code-question;
		}
		&.sfwd-quiz {
			background: $code-parent;
			.ld__builder--final-quizzes & {
				background: $code-grandchild;
			}
		}
	}

	&.-grand-child {
		background: $code-grandchild;
		font-size: 10px;
		height: 18px;
		min-width: 18px;
		width: 18px;
		&.sfwd-quiz {
			background: $code-child;
		}
	}
}

.ld__builder--parent {
	padding: 20px 10px;
	border-top: 1px solid $alto;

	&:first-child {
		border-top: 0;
	}

	&.-active {
		background: $wild_sand;
		border: 1px solid $allports;
	}

	&.-active + .ld__builder--parent {
		border-top: 0;
	}
}

.ld__builder--child {
	border-left: 1px dashed $verdun_green;
	display: none;
	margin: 0 0 0 10px;
	padding: 10px 0 10px 10px;

	&.-question {
		border-left-color: $code-question;
	}

	&.-expanded {
		display: block;
	}
}

.ld__builder--child-item,
.ld__builder--grandchild-item {
	padding: 4px 0;
	h3 {
		font-size: 15px;
	}
}

.ld__builder--grandchild-item {
	min-height: 30px;

	.ld-node-header__drag-handle,
	.ld-node-header__drag-handle span {
		height: 18px;
	}
}

.ld__builder--grandchild {
	border-left: 1px dashed $grenadier;
	margin: 0 0 10px 10px;
	padding: 10px 0 10px 20px;
}

/* New Entity forms */
.ld__builder--new-entities {
	display: flex;
	flex-wrap: wrap;
	margin: 0 0 0 10px;

	&.-question {
		margin-left: 27px;
	}

	form {
		display: flex;
	}

	input[type="text"] {
		border-radius: 4px;
		border: 1px solid #8d96a0;
		box-shadow: 0 0 0 transparent;
		flex: 1;
		font-size: 13px;
		height: 28px;
		padding: 6px 8px;
		transition: box-shadow .1s linear;
	}
}

.ld__builder--new-entity {
	margin: 0 20px 10px 0;
	&.-expanded {
		flex: 1 0 100%;
	}

	&.-matrix form {
		flex-direction: column;
		@media (min-width: 1120px) {
			flex-direction: row;
		}
	}

	svg {
		display: inline-block;
		fill: $allports;
		height: 20px;
		margin: 0 5px 0 0;
		vertical-align: bottom;
		width: 20px;
	}

	a {
		font-weight: bold;
	}
}

.ld__builder--new-entity-button {
	margin: 0 5px 0 0;
}

.ld__builder--form-error {
	color: $persian_red;
	display: block;
	flex: 1 0 100%;
	font-size: 0.8rem;
	margin: 10px 0 0 5px;
}

.ld__builder--form-notice {
	display: inline-block;
	font-size: 13px;
	font-weight: normal;
	margin: 0 0 0 5px;
}

.ld__builder--empty-entity {
	background: $porcelain;
	padding: 30px 10px;
	text-align: center;

	p:not( :first-child ) {
		font-weight: normal;
	}
}

.ld__builder--section-heading {
	background-color: #ddd;
	border-bottom: 1px solid $shuttle_grey;
	border-top: 2px solid $shuttle_grey;

	.ld-node-header__order button {
		background-color: #ddd;
	}

	h3 button {
		text-transform: uppercase;
	}
}
