@media (min-width: 782px) {
	body.learndash-screen.is-fullscreen-mode .edit-post-sidebar {
		top: 214px;
	}
}

#sfwd-quiz-questions,
#sfwd-course-lessons,
#sfwd-course-topics,
#sfwd-course-quizzes {
	h2.hndle.ui-sortable-handle,
	h2.hndle.ui-sortable-handle,
	h2.hndle.ui-sortable-handle,
	h2.hndle.ui-sortable-handle {
		pointer-events: none;
		display: block; /* Adjust positioning after WP 5.5 changes */
	}
}


.ld__builder-sidebar-widget {
	font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;

	ul {
		list-style-type: none;
		margin: 13px 0;
		max-height: 300px;
		overflow-y: scroll;
		padding: 0;

		&.-recently-edited {
			max-height: none;
			overflow: visible;
		}
	}

	li {
		font-size: 13px;
		list-style-type: none;
		margin: 0 0 6px;
	}

	input[type="text"] {
		font-size: 13px;
	    padding: 6px 8px;
	    box-shadow: 0 0 0 transparent;
	    transition: box-shadow .1s linear;
	    border-radius: 4px;
	    border: 1px solid #8d96a0;
	    width: 100%;
	}

	input[type="checkbox"] {
		margin: 0 12px 0 0;
	}
}

.ld__builder-sidebar-header {
	display: flex;
	justify-content: space-between;
	margin: 5px 0 0;
}

.ld__builder-sidebar-item {
	align-items: center;
	display: flex;
	justify-content: space-between;
	min-height: 28px;

	&:hover {
		background-color: #f0f0f0;
		box-shadow: 0 0 0 5px #f0f0f0;

		.is-button {
			opacity: 1;
			visibility: visible;
		}
	}

	.is-button {
		opacity: 0;
		visibility: hidden;
		margin-left: 5px;
	}
}

.ld__builder-sidebar-widget {
	.ld__builder-sidebar-more,
	.ld__builder-sidebar-refresh {
		display: block;
		margin: auto;
		background: none;
		border: none;
		color: #00a0d2;
	}
}

.ld-link-block {
	display: block;
	margin-top: 10px;
}
