.ld__builder--app,
.ld__builder-sidebar-widget {
	font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
	
	* {
		box-sizing: border-box;
	}

	a {
		font-size: 13px;
		color: $allports;
		text-decoration: none;

		&:hover {
			color: $cerulean;

			svg {
				fill: $cerulean;;
			}
		}

		&.ld__builder-sidebar-header-active {
			color: $shuttle_grey;
			cursor: text;
			font-weight: bold;
		}
	}

	p {
		font-size: 13px;
	}

	ul, li {
		list-style-type: none;
	}

	button,
	input[type="button"],
	input[type="submit"] {
		font-size: 13px;
		padding: 0 10px 1px;
	    line-height: 26px;
	    height: 28px;
	    border-radius: 3px;
	    white-space: nowrap;
	    border-width: 1px;
	    border-style: solid;
	    vertical-align: top;
		margin: 0 0 0 3px;
		cursor: pointer;

	    &.is-primary {
		    background: #0085ba;
		    border-color: #006a95 #00648c #00648c;
		    box-shadow: inset 0 -1px 0 #00648c;
		    color: #fff;
		    text-decoration: none;
		    text-shadow: 0 -1px 1px #005d82, 1px 0 1px #005d82, 0 1px 1px #005d82, -1px 0 1px #005d82;
		}

		&.is-default {
			color: #555;
		    border-color: #ccc;
		    background: #f7f7f7;
		    box-shadow: inset 0 -1px 0 #ccc;
		}
	}

	.mce-btn button {
		border: none;
		margin-left: 0;
		height: auto;
	}
}

// Fallback styles for when wp.editor.initialize is undefined.
.ld-answer-rt-editor textarea {
    width: 100%;
    height: 80px;
}