.ld-entity-builder-header {
	align-items: center;
	background: #fff;
	border-bottom: 1px solid $shuttle_grey;
	display: flex;
	justify-content: space-between;
	margin: 10px 0 20px;
	min-height: 28px;
	padding: 10px;
	position: sticky;
	top: 0;
	z-index: 12;

	h2 {
		color: $shuttle_grey;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.4;
		margin: 0 !important;
		padding: 0 !important;
	}

	.ld-undo-button {
		-webkit-appearance: none;
		background: transparent;
		border: 0;
		color: #0073aa;
		margin: 0 15px 0 auto;
		padding: 0;
		&:active,
		&:hover {
			color: #00a0d2;
			svg {
				fill: #00a0d2;
			}
		}
		svg {
			display: inline-block;
			fill: #0073aa;
			margin: 0 5px 0 0;
			vertical-align: middle;
			width: 14px;
		}
	}
}