.ld-question-answer__order {
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-right: 10px;
}

.ld-question-answer {
	&.sfwd_options {
		margin: 0;
	}
	button.ld-button-reset {
		cursor: pointer;

		&:hover {
			.edit-svg {
				fill: $wp-blue;
			}
		}
	}
	&.-showform {
		align-items: flex-start;
	}
	.ld-node-header__drag-handle {
		align-items: center;
		display: flex;
		margin-right: 0;
	}
	.ld-move-up,
	.ld-move-down {
		-webkit-appearance: none;
		align-items: center;
		background-color: transparent;
		border: 0;
		display: flex;
		height: 10px;
		height: 14px;
		justify-content: center;
		line-height: 14px;
		margin: 0;
		padding: 0;
	}
	&:first-child {
		.ld-move-up {
			display: none;
		}
	}
	&:last-child {
		.ld-move-down {
			display: none;
		}
	}
	textarea {
		width: 100%;
		margin-bottom: 10px;
	}

	.row-actions,
	.ld-radio-input-wrapper {
		margin-left: auto;
	}

	.ld-radio-input-wrapper + .row-actions {
		margin-left: 0;
	}

	.ld-answer-group .ld-radio-input-wrapper {
		margin-left: 0;
	}

}

.ld-answer-value {
	&.-correct {
		font-weight: 700;
	}
	img {
		height: auto;
		max-width: 300px;
	}
}

.ld-answer-group {
	p {
		margin-top: 0;
		font-weight: 700;
	}
}

.ld-answer-editor {
	align-items: flex-start;
	display: flex;
	flex: 1;
	flex-direction: column;
	padding-right: 20px;
}

.ld-answer-editor-table {
	align-items: flex-start;
	display: flex;
	justify-content: space-between;
	margin-bottom: 15px;
	width: 100%;
	> div {
		flex: 1;
		padding: 0 15px 0 0;
		position: relative;

		& svg {
			fill: #999;
			position: absolute;
			right: 3px;
			top: 15px;
		}
	}
}

.ld-answer-editor-actions {
	align-items: center;
	display: flex;
	margin: 10px 0;
	width: 100%;

	&.-essay {
		align-items: flex-start;
		justify-content: flex-start;

		.ld-answer-group:last-child {
			padding-left: 30px;
		}
	}

	&.-matrix {
		height: 28px;
		justify-content: space-between;
		margin-top: 0;

		.ld-switch-wrapper {
			margin-left: 0;
			margin-right: auto;
		}
	}

	.ld-switch-wrapper {
		margin-left: auto;
		margin-right: 10px;
		margin-top: 0;
		&.html {
			.ld-switch {
				margin-right: 10px !important;
			}
		}
	}

	.ld-trash {
		display: inline-block;
		margin-left: 8px;
		margin-right: 8px;
	}
}

.ld__builder--question-settings {
	margin: 15px 0 0;
	width: 85%;
}

.ld__builder--show-settings {
	color: $wp-blue;
	cursor: pointer;

	&:focus,
	&:hover {
		color: $wp-blue-hover;

		svg {
			fill: $wp-blue-hover;
		}
	}
}

.ld__builder--edit-content {
	align-items: center;
	cursor: pointer;
	display: flex;
	margin: 15px 0;


	&:focus .edit-svg,
	&:hover .edit-svg {
		fill: $wp-blue;
	}

	.edit-svg {
		margin-left: 10px;
		vertical-align: middle;
	}

	> span {
		display: inline-block;
		font-weight: 700;
		margin-right: 10px;
	}

	.warning {
		color: $persian_red;
	}
}

.ld-answer-essay-settings {
	display: block;
	strong {
		margin-right: 10px;
	}
}


.ld__builder--edit-content-value,
.ld__builder--edit-content-value > * {
	margin: 0;
	max-width: 400px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.ld__builder--question-label-value {
	display: flex;

	> div {
		flex-basis: 30%;

		&:last-child {
			flex: 1;
		}
	}

	label {
		display: block;
		font-weight: bold;
		margin: 0 0 15px 0;
	}
}

@keyframes LDFadeIn {
	from { opacity: 0; }
	to { opacity: 1; }
}
.ld-answer-updated {
	animation: LDFadeIn .5s ease-in 0s 1;
	margin-left: 10px;
}

.ld-answer-read-only {
	align-items: flex-start;
	cursor: pointer;
	display: flex;
	justify-content: flex-start;

	> span {
		display: inline-block;
		font-weight: 700;
		margin-right: 10px;
	}

	&:hover svg {
		fill: $wp-blue;
	}

	pre,
	p {
		margin: 0;
	}

	&.-essay {
		flex-direction: column;
	}
}

.ld-button-matrix-edit > svg {
	fill: #999;
	margin: 0 5px;
	vertical-align: middle;
}
