#learndash_builder_box_wrap {
	border: 0;
	display: block;

	.ld-button-reset {
		-webkit-appearance: none;
		background: transparent;
		border-radius: 0;
		border: 0;
		font-size: inherit;
		height: auto;
		line-height: inherit;
		margin: 0;
		padding: 0;
		text-align: left;
		vertical-align: inherit;
		white-space: inherit;
	}

	.ld-switch-wrapper {
	  display: flex;
	}

	.ld-switch {
	  margin-right: 16px;
	  position: relative;
	}

	.ld-switch__input {
	  border: none;
	  height: 100%;
	  left: 0;
	  margin: 0;
	  opacity: 0;
	  padding: 0;
	  position: absolute;
	  top: 0;
	  width: 100%;
	  z-index: 1;
	}

	.ld-switch__track {
	  background-color: #fff;
	  border-radius: 9px;
	  border: 2px solid #6c7781;
	  box-sizing: border-box;
	  content: "";
	  display: inline-block;
	  height: 18px;
	  transition: .2s background ease, .2s border ease;
	  vertical-align: top;
	  width: 36px;
	}

	.ld-switch__thumb {
	  background-color: #6c7781;
	  border-radius: 50%;
	  border: 5px solid #6c7781;
	  box-sizing: border-box;
	  display: block;
	  height: 10px;
	  left: 4px;
	  position: absolute;
	  top: 4px;
	  transition: .1s transform ease;
	  width: 10px;
	}

	.ld-switch__on-off {
	  border-radius: 100%;
	  border: 2px solid #6c7781;
	  box-sizing: border-box;
	  display: block;
	  height: 6px;
	  position: absolute;
	  right: 6px;
	  top: 6px;
	  width: 6px;
	}

	.ld-switch__input:checked ~ .ld-switch__track {
	  background-color: #11a0d2;
	  border-color: #11a0d2;
	}

	.ld-switch__input:checked ~ .ld-switch__thumb {
	  background-color: #fff;
	  border-width: 0;
	  transform: translateX(18px);
	}

	.ld-switch__input:checked ~ .ld-switch__on-off {
	  background-color: #fff;
	  border-radius: 0;
	  border: 0;
	  height: 6px;
	  left: 8px;
	  right: auto;
	  width: 2px;
	}

	.ld-switch__input:focus ~ .ld-switch__track {
	  box-shadow: 0 0 0 2px #fff, 0 0 0 3px #6c7781;
	  outline-offset: 2px;
	  outline: 2px solid transparent;
	}

	.ld-switch__input:hover ~ .ld-switch__track {
	  border-color: #11a0d2;
	}

	.ld-switch__input:checked:hover ~ .ld-switch__track {
	  background-color: #6c7781;
	  border-color: #6c7781;
	}

	.ld-switch.-disabled ~ label,
	.ld-switch__input[disabled="disabled"] {
	  pointer-events: none;
	}

	.ld-switch__input[disabled="disabled"] {
	  opacity: 0 !important;
	}

	.ld-switch__input[disabled="disabled"] ~ .ld-switch__track {
	  opacity: 0.5;
	}
}
