/* Chevron up/down */
.ld-node-header__order {
	position: relative;
	button {
		background-color: #fff;
		border: 0;
		cursor: pointer;
		height: auto;
		left: 0;
		line-height: 1;
		margin: 0;
		padding: 0;
		position: absolute;
		width: 18px;
		.-active & {
			background-color: #f5f5f5;
		}
	}
	button:first-of-type {
		top: -17px;
	}

	button:last-of-type {
		bottom: -17px;
	}

	.ld__builder--question:last-of-type > .ld-node-header & button:last-of-type,
	.ld__builder--question:first-of-type > .ld-node-header & button:first-of-type,
	.ld__builder--lesson:last-of-type > .ld-node-header & button:last-of-type,
	.ld__builder--lesson:first-of-type > .ld-node-header & button:first-of-type,
	.ld__builder--child-item:nth-last-of-type(2) > .ld-node-header & button:last-of-type,
	.ld__builder--child-item:first-of-type > .ld-node-header & button:first-of-type,
	.ld__builder--grandchild-item:nth-last-of-type(2) > .ld-node-header & button:last-of-type,
	.ld__builder--grandchild-item:first-of-type > .ld-node-header & button:first-of-type {
		opacity: 0;
		visibility: hidden;
	}
}

/* Counter */
.ld-node-header__counter {
	font-size: 14px;
	font-weight: 400;
}

/* Drag handle */
.ld-node-header__drag-handle {
	display: inline-block;
	height: 8px;
	margin-right: 10px;
	width: 18px;

	span {
		align-items: center;
		display: inline-flex;
		height: 8px;
		justify-content: center;
		width: 18px;
	}
}

/* Row styling */
.ld-node-header {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;

	@media (min-width: 1300px) {
		flex-wrap: nowrap;
	}

	h2 {
		padding: 0;
	}

	h3 {
		flex: 1;
		margin: 0;
	}

	h2,
	h3 {
		button {
			cursor: text;

			svg {
				cursor: pointer;
				fill: $wp-blue;
			}
		}
	}

	&:hover .row-actions {
		position: static;
	}

	.row-actions {
		margin-left: auto;
		&.-right {
			min-width: 88px;
		}
	}

	form {
		display: flex;
		flex-wrap: wrap;
		padding-right: 10px;

		input[type="text"] {
			flex: 1;
		}

		&.ld-node-header__points-form {
			align-items: center;
			display: inline-flex;
		}
	}

	.ld__builder--form-notice {
		flex: 1 0 100%;
	}

	.toggle {
		cursor: pointer;
		display: inline-block;
		font-weight: bold;
		font-size: 1rem;
		margin: 0 0 0 20px !important;
		transition: transform 0.2s ease-in-out;
		transform: rotate( 0deg );
	}

	.toggle.-expanded {
		transform: rotate( 180deg );
	}

	.edit-svg {
		margin-left: 4px;
		vertical-align: middle;
	}

	.trash {
		a {
			color: $persian_red;
		}
	}
}

.ld-node-header__quiz-actions {
	box-sizing: content-box;
	flex: 1 0 calc(100% - 100px);
	max-width: calc(100% - 100px);
	padding-left: 61px;
	text-align: right;

	@media (min-width: 1300px) {
		flex: 0 0 auto;
		max-width: none;
		padding-left: 0;
	}
}

.ld-question-points {
	margin: 0 3px 0 5px;
	vertical-align: middle;
	width: 70px;
}

.ld-node-header__points-form-toggle {
	cursor: pointer;

	&:hover svg {
		fill: $wp-blue;
	}
}

.select-question-type {
	margin: 0 10px 0 0;
	vertical-align: middle;
}

.ld-button-reset.warning-expand {
	svg {
		fill: $persian_red;
	}
}

.warning-icon {
	color: $persian_red;
	display: inline-block;
	vertical-align: middle;
	&.-header {
		margin-left: 8px;
	}
	&.-question svg {
		margin-right: 5px;
	}
}

.warning-icon svg {
	fill: $persian_red;
	height: 16px;
	width: 16px;
}
