.ld__builder--quiz-footer {
	display: flex;

	> .ld__builder--new-entities {
		flex: 1;
		padding-right: 20px;
	}

	> div {
		&:last-child {
			font-weight: bold;
			padding: 0 34px 0 0;
			text-align: right;
		}
	}
}

.ld-answer-rt-editor {
	margin: 0 0 15px;
	width: 100%;

	.html-active {
		textarea {
			width: 100%;
		}
	}
}

.ld-answer-richtext-description {
	width: 100%;
}
@media (min-width: 1200px) {
	.ld-answer-richtext-description {
		display: flex;
		justify-content: space-between;

		.ld-answer-rt-editor {
			flex: 1 0 65%;
			max-width: 65%;
			width: 65%;
		}

		.ld-answer-descriptions {
			flex: 1 0 35%;
			max-width: 35%;
			width: 35%;
			padding-left: 20px;
		}
	}
}

.ld__builder--question.-locked {
	position: relative;

	&:before {
		content: '';
		display: block;
		background: rgba(255,255,255,0.5);
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 10;
	}
}

.ld__builder--question-settings,
.ld__builder--question-content,
.ld-question-answer {
	&.-editing {
		position: relative;
		z-index: 11;
	}
}

.ld-answer-descriptions .description {
	font-style: normal;
}
.description-red {
	color: #c92c2c;
}
